import {Link} from "react-router-dom";
import {useContext, useState} from "react";
import getLang from '../lang'
import {DownArrow} from '../icons/Icons'
import Main from "../Context/Main";
const Navbar = () => {

    const {lang} = useContext(Main)
    const [active,setActive] = useState(false)
    const [selected, setSelected] = useState('');

    const changeLang = lang => {
        localStorage.setItem('lang',lang)
        window.location.reload()
    }

    const activeHandle = () => {
        setActive(!active)
        document.querySelector('body').style.overflow = !active ? 'hidden' : 'auto'
    }

    return (
        <nav className="custom-navbar" id="top">
            <Link to="/" className="logo">
                <img src="./images/logo.png" alt="Three Steps"/>
            </Link>
            <ul className="menu">
                <li>
                    <a href="#services">Xidmətlər</a>
                </li>
                <li>
                    <a href="#technology">Texnologiyalar</a>
                </li>
                <li>
                    <a href="#about">Haqqımızda</a>
                </li>
                <li>
                    <a href="#footer">Əlaqə</a>
                </li>
            </ul>
            <ul className="right-menu">
                <li className="dropdown">
                    <div tabIndex="1">
                        {getLang()}
                        <DownArrow/>
                        <ul className={`custom-dropdown-menu`}>
                            {['az','en','ru'].map((item,index)=>
                                    item !== lang && (
                                        <li key={index} onClick={() => changeLang(item)}>{item.toUpperCase()}</li>
                                    )
                            )}
                        </ul>
                    </div>
                </li>
                <li>
                    <a href="#contact" className="nav-btn">Müraciət et</a>
                </li>
            </ul>
            <div className={`hamburger ${active && 'active'}`} onClick={activeHandle}>
                <span className="bar"></span>
                <span className="bar"></span>
                <span className="bar"></span>
            </div>
            <ul className={`mobile-menu ${active && 'active'}`}>
                <li>
                    <a href="#services" onClick={activeHandle}>Xidmətlər</a>
                </li>
                <li>
                    <a href="#technology">Texnologiyalar</a>
                </li>
                <li>
                    <a href="#about">Haqqımızda</a>
                </li>
                <li>
                    <a href="#footer" onClick={activeHandle}>Əlaqə</a>
                </li>
                <li className="position-relative">
                    <select onChange={() => changeLang(document.querySelector('li.position-relative select').value)}>
                        {['az','en','ru'].map((item,index)=>
                            <option value={item} selected={item === lang && true} key={index}>{item.toUpperCase()}</option>

                        )}
                    </select>
                    <DownArrow color="#09053F"/>
                </li>
            </ul>
        </nav>
    )
}
export default Navbar
