import {DownArrow} from "../icons/Icons";
import {useRef} from "react";

const CustomSelect = ({menu,value,setValue}) => {
    const select = useRef(null)
    const handleChange = id => {
        select.current.blur()
        setValue(id)
    }

    return(
            <div className="custom-select" ref={select} tabIndex={1}>
                <p>{menu.find(item => item.id === value).name}</p>
                <DownArrow color="#222"/>
                <ul className="menu">
                    {menu.filter(item => item.id !== value).map(item => (
                        <li key={item.id} onClick={e => handleChange(item.id)}>
                            {item.name}
                        </li>
                    ))}
                </ul>
            </div>

    )
}
export default CustomSelect
