import CustomSelect from "./CustomSelect";
import {useEffect, useState} from "react";

const Contact = ({menu,value,setValue}) => {
    const [style,setStyle] = useState({
        backgroundImage:`url('./images/contact-bg${window.innerWidth < 1320 ? '2' : ''}.png')`,
    })

    useEffect(() => {
        window.addEventListener('resize',() => {
            const s = {
                backgroundImage:`url('./images/contact-bg${window.innerWidth < 1320 ? '2' : ''}.png')`,
            }
            setStyle(s)
        })
    },[])

    return(
        <section className="contact" id="contact" style={style}>
            <div className="container-fluid">
                <div className="row justify-content-between align-items-center">
                    <div className="col-12 col-lg-5">
                        <div className="text-center header" data-aos="fade-right" data-aos-duration="800">
                            <h6>Get started with us </h6>
                            <p>Don't bother to get started us, we are ready to help you 24/7. Please fill in the column on the right to get started</p>
                        </div>
                    </div>
                    <div className="col-12 col-lg-5">
                        <div className="contact-box" data-aos="flip-right" data-aos-duraion="800">
                            <div className="header">Bizə yazın</div>
                            <form action="" className="form">
                                <CustomSelect menu={menu} value={value} setValue={setValue}/>
                                <div className="form-element">
                                    <input type="text" placeholder="Ad/soyad"/>
                                </div>
                                <div className="form-element">
                                    <input type="text" placeholder="Nömrə"/>
                                </div>
                                <div className="form-element">
                                    <input type="text" placeholder="Email ünvan"/>
                                </div>
                                <div className="form-element">
                                    <textarea rows={5} placeholder="Mesaj..."></textarea>
                                </div>
                                <button className="site-btn">Göndər</button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}
export default Contact
