import {useRef, useState} from "react";

const Technology = () => {
    const [active, setActive] = useState(false)
    const headerRef = useRef(null)
    const logos = [
        {
            alt: 'Html',
            name: 'html.png'
        },
        {
            alt: 'Css',
            name: 'css.png'
        },
        {
            alt: 'Bootstrap',
            name: 'bootstrap.png'
        },
        {
            alt: 'JavaScript',
            name: 'js.png'
        },
        {
            alt: 'React',
            name: 'react.png'
        },
        {
            alt: 'React Native',
            name: 'react-native.png'
        },
        {
            alt: 'Vue',
            name: 'vue.png'
        },
        {
            alt: 'Vuetify',
            name: 'vuetify.png'
        },
        {
            alt: 'Angular',
            name: 'angular.png'
        },
        {
            alt: 'Flutter',
            name: 'flutter.png'
        },
        {
            alt: 'Swift',
            name: 'swift.png'
        },
        {
            alt: 'Php',
            name: 'php.png'
        },
        {
            alt: 'Laravel',
            name: 'laravel.png'
        },
        {
            alt: 'Java',
            name: 'java.png'
        },
        {
            alt: 'C Sharp',
            name: 'c-sharp.png'
        },
        {
            alt: 'Net Core',
            name: 'net-core.png'
        },
        {
            alt: 'Node',
            name: 'node.png'
        },
        {
            alt: 'Python',
            name: 'python.png'
        },
        {
            alt: 'Kotlin',
            name: 'kotlin.png'
        },
        {
            alt: 'Sql Server',
            name: 'ms-sql.png'
        },
        {
            alt: 'MySql',
            name: 'my-sql.png'
        },
        {
            alt: 'Postegre Sql',
            name: 'postegre-sql.png'
        },
        {
            alt: 'Oracle Sql',
            name: 'oracle.png'
        },
        {
            alt: 'Git',
            name: 'git.png'
        },
        {
            alt: 'GitLab',
            name: 'git-lab.png'
        },
        {
            alt: 'npm',
            name: 'npm.png'
        },
    ]

    const toggleLogo = (e) => {
        if (active) {
            headerRef.current.scrollIntoView()
        } else {
            e.target.scrollIntoView()
        }
        setActive(!active)
    }
    return (
        <section className="technology" id="technology">
            <div className="container-fluid">
                <div className="row">
                    <div className="col-12">
                        <div className="header" data-aos="fade-right" data-aos-duration="800" ref={headerRef}>
                            <h5>Texnologiyalar</h5>
                            <p>Let's see some of our clients</p>
                        </div>
                    </div>
                </div>
                <div className="row logos">
                    {logos.map((item, index) => (
                        <div key={index} className="col-4 col-md-3">
                                <div className={`${active ? 'd-flex' : ((index < (window.innerWidth < 992 ? 9 : 8)) ? 'd-flex' : 'd-none')} align-items-center justify-content-center`}>
                                    <div className="logo" data-aos="flip-right" data-aos-duraion="800">
                                        <img src={`./logos/${item.name}`} alt={item.alt}/>
                                    </div>
                                </div>

                        </div>
                    ))}
                </div>
                <div className="row justify-content-center">
                    <div className="col-12 col-md-4">
                        <div className="load-more text-center">
                            <button onClick={toggleLogo}>{!active ? 'Daha çox' : 'Daha az'}</button>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}
export default Technology
