const Hero = () => {
    return(
        <section className="hero">
            <div className="container-fluid">
                <div className="left-bg" data-aos-duration="800" data-aos="fade-down" style={{backgroundImage:`url('./images/bg-left.png')`}}></div>
                <div className="row">
                    <div className="col-12 col-md-6" data-aos="fade-right" data-aos-duration="800">
                        <div className="content">
                            <h1 className="title">Solutions for the growing world.</h1>
                            <p className="sub-title d-none d-md-block">It has survived not only five centuries,
                                but also the leap into electronic
                                typesetting, remaining essentially
                                unchanged.
                            </p>
                            <a href="#contact" className="hero-btn d-none d-md-inline-block">Müraciət et</a>
                        </div>
                    </div>
                    <div className="col-12 col-md-6" data-aos="fade-up" data-aos-duration="800">
                        <div className="right d-flex align-items-center justify-content-center">
                            <div className="hero-img" >
                                <img src="./images/hero.png" alt="Hero"/>
                            </div>
                        </div>
                        <div className="d-flex flex-column d-md-none">
                            <h2>It has survived not only five centuries,
                                but also the leap into electronic
                                typesetting, remaining essentially
                                unchanged.</h2>
                            <a href="#contact" className="hero-btn w-100 d-none d-md-block">Müraciət et</a>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}
export default Hero
