import {UpArrow} from "./icons/Icons";
import Aos from 'aos'
import '../node_modules/aos/dist/aos.css'
import {useContext, useEffect, useState} from "react";
import Hero from "./components/Hero";
import WhoWeAre from "./components/WhoWeAre";
import Services from "./components/Services";
import Main from "./Context/Main";
import Contact from "./components/Contact";
import Technology from "./components/Technology";
const Home = () => {

    const {toTop} = useContext(Main)
    const [check,setCheck] = useState(false)
    const [value,setValue] = useState(1)

    const menu = [
        {
            id:1,
            name:'General'
        },
        {
            id:2,
            name:'Developement'
        },
        {
            id:3,
            name:'Ux/Ui Design'
        },
        {
            id:4,
            name:'Digital markteing'
        }
    ]

    const topHandle = () => {
        window.scrollTo(0,0)
    }

    useEffect(() => {
        if(!check){
            Aos.init()
            setCheck(true)
        }
    },[])

    return(
        <>
            <div className={`to-top ${toTop && 'active'}`} data-aos-duration="800" data-aos={`fade-${toTop ? 'down' : 'up'}`} onClick={topHandle}>
                <UpArrow/>
            </div>
            <Hero/>
            <Services setValue={setValue}/>
            <WhoWeAre/>
            <Technology/>
            <Contact menu={menu} value={value} setValue={setValue}/>
        </>
    )
}
export default Home
