import {DownArrow} from "../icons/Icons";

const Services = ({setValue}) => {

    const goToContact = (id) => {
        setValue(id)
        document.querySelector('section.contact').scrollIntoView()
    }

    return (
        <section className="services">
            <div className="container-fluid">
                <div className="row">
                    <div className="col-12">
                        <h2 data-aos="fade-right" data-aos-duration="800" id="services">
                            3 steps sizə
                            <br/>
                            <span>təklif edir!</span>
                        </h2>
                    </div>
                </div>
                <div className="row service-row">
                    <div className="col-lg-4 col-12">
                        <div className="d-flex flex-column service service-one" data-aos="fade-up" data-aos-duration="400">
                            <div className="service-img">
                                <img src="./images/development.jpg" alt="Development"/>
                            </div>
                            <div className="info">
                                <div className="header">Devlopment</div>
                                <p>Add WooCommerce plugin to any WordPress site and set up a new store in
                                    minutes.</p>
                                <div className="align-items-center" onClick={e => goToContact(2)}>
                                    Müraciət et
                                    <DownArrow color="#7854F7"/>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4 col-12">
                        <div className="d-flex flex-column service service-transform-one"  data-aos="fade-up" data-aos-duration="1000">
                            <div className="service-img">
                                <img src="./images/development.jpg" alt="Development"/>
                            </div>
                            <div className="info">
                                <div className="header">Qrafik və Ux dizayn</div>
                                <p>Add WooCommerce plugin to any WordPress site and set up a new store in
                                    minutes.</p>
                                <div className="align-items-center" onClick={e => goToContact(3)}>
                                    Müraciət et
                                    <DownArrow color="#7854F7"/>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4 col-12">
                        <div className="d-flex flex-column service service-transform-two" data-aos="fade-up" data-aos-duration="1600">
                            <div className="service-img">
                                <img src="./images/development.jpg" alt="Development"/>
                            </div>
                            <div className="info">
                                <div className="header">Rəqəmsal Market</div>
                                <p>Add WooCommerce plugin to any WordPress site and set up a new store in
                                    minutes.</p>
                                <div className="align-items-center" onClick={e => goToContact(4)}>
                                    Müraciət et
                                    <DownArrow color="#7854F7"/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}
export default Services
